.overlay {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  
  }
  
  .content {
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    min-width: 300px;
    max-width: 700px;
    width: 70vw;
    position: relative;
  }
  
  // =========================================================
  
  .contentChange {
    min-width: 300px;
    max-width: 700px;
    position: relative;
  }